import React from 'react';
import { Carousel } from 'antd';
import { Browser } from "react-window-ui";

function renderDisplayCase(imgArray, _className, elements) {
    for (let i = 0 ; i < imgArray.length ; imgArray.length <= 3 ? i++ : i = i + 2) {
         elements.push(<div>
            {imgArray.length <= 3 ? (
                <div className='imgBlockContainer'>
                    <Browser className='imgContainer'>
                        <img className={_className} src={imgArray[i]}/>
                    </Browser>
                </div>
            ) : (
                <div className='imgBlockContainer'>
                    <Browser className='imgContainerDouble'>
                        <img className={_className} src={imgArray[i]}/>
                    </Browser>
                    <Browser className='imgContainerDouble'>
                        <img className={_className} src={imgArray[i + 1]}/>
                    </Browser>
                </div>
            )}
        </div>);
    }
}

const displayCase = props => {
    const { type, title, subHeader, imgArray } = props;
    const _className = 'fillContainer';
    let elements = [];

    renderDisplayCase(imgArray, _className, elements);

    return (
        <div>
            <div className='expText' style={{ marginBottom: 0, marginTop: '5%' }}>
                <p style={{ marginBottom: -5 }}>{type}</p>
                <p style={{ fontSize: '2em'}}>{title}</p>
                <p style={{ fontSize: '1.5em' }}>{subHeader}</p>
            </div>
            <div>
                <Carousel>
                    {elements.map(element => {
                        return element;
                    })}
                </Carousel>
            </div>
        </div>
    )
};

export default displayCase;